*{
    box-sizing: border-box;
}

a{
    font-family: 'Quicksand';
    font-size: 1vw;
    text-decoration: none;
    color: black;
}

.wrapper{
    margin-left: 2%;
    float: none;
    text-align: center;
}

header li{
    display: inline-block;
    position: relative;
}

header li a{
    display: block;
    font-size: 1vw;
    padding: 20px;
}

header li a:hover{
    background-color: #f39523;
    color: white;
}

header li:hover .submenu{
    display: block;
    width: 150%;
    color: white;
}

.submenu{
    display: none;
    position: absolute;
    background-color: #f9ca91;
    color: white;

    
}

