
.form1{
    margin-left: 10%;
    margin-right: 10%;
    margin-top: 1%;
    height: 60%;
}

.form2{
    text-align: center;
    margin-top: 2%;
    font-family: 'Quicksand';
    font-size: 1.3vw;
    color: #333;
    margin-bottom: 5%;
}

.cv {
    width: 15%;
    float: right;
    margin-right: 20%;
    margin-top: 3%;
}

.imgcv{
    width: 100%;
    cursor: pointer;
}

.alert {
    text-align: center;
}

.success, .error {
    font-size: 1.3vw;
}

.success {
    color: green;
}

.error {
    color: red;
}

.ami1{
    width: 30%;
    margin-left: 30%;
    margin-top: 3%;
    font-family: inherit;
    border: 0;
    border-bottom: 2px solid gray;
    outline: 0;
    font-size: 1.3vw;
    padding:  7px 0;
    background: transparent;
    transition: border-color 0.2s;
}

.btn{
    display: inline-block;
    font-family: 'Quicksand';
    letter-spacing: 1px;
    box-shadow: none;
    appearance: none;
    border: 0;
    outline: 0;

    height: 45px;
    line-height: 42px;
    padding: 0 30px;
    font-size: 1.2vw;
    border-radius: 30px;
    color: white;
    margin-left: 30%;
    cursor: pointer;
    transition: all .5s;
    transition-timing-function: cubic-bezier(.2, 3, .4, 1);
    user-select: none;
    width: 30%;
}

.btn:hover{
    transform: scale(1.1, 1.1);
}

.btn:active{
    transform: scale(1.05, 1.05);
}